<script>
import dayjs from 'dayjs'
import TimeLine from './index.js'

export default{
  name: 'App',
  components: {},
  data() {
    return {
      timeline:null,
      buttons : {
        left: {
          disabled: false,
          target: null
        },
        right: {
          disabled: true,
          target: null
        }
      },
      textDate: '2012/11/15',
      textTime: '20:35:24',
      callbackData: ''
    }
  },
  mounted(){
    this.setTimeLine()
  },
  methods: {
    setPointerTime(time) {
      this.textDate = time.format('YYYY/MM/DD')
      this.textTime = time.format('HH:mm:ss')

      this.buttons.left.target = time.subtract(24, 'h').startOf('day')
      this.buttons.right.target = time.add(24, 'h').startOf('day').add(1, 's')

      const nowZero = dayjs().startOf('day')
      this.buttons.right.disabled = nowZero.isSame(time) || nowZero.isBefore(time)
    },
    setPointerVideoList(list) {
      this.callbackData = JSON.stringify(list)
    },
    setTimeLine() {
      const now = Math.floor(Date.now() / 1000)

      const option = {
        container: document.querySelector('#time-line'),
        dayContainer: document.querySelector('#day-line'),
        pointerColor: 'rgba(255, 58, 51, 1)',
        record: [
          {
            start: now + 30,
            duration: 2400,
            info: '11111'
          },
          {
            start: now + 3600,
            duration: 1800,
            info: '2222'
          }
        ],
        pointerTimeCallback: this.setPointerTime,
        pointerBlockDataCallback: this.setPointerVideoList
      }

      this.timeline = new TimeLine(option)
    },
    backToNow() {
      const endTime = dayjs()

      this.timeline.jumpTime(endTime)
    },
    jumpDay(direction) {
      const button = this.buttons[direction]

      if (button.disabled) return
      button.target && this.timeline.jumpTime(button.target)
    }
  }
}
</script>

